body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cover-title {
  position: absolute;
  z-index: 10;
  left: 0;
  bottom: 0px;
  right: 0;
  margin: 0 !important;
  padding: 15px !important;
  background: rgba(0,0,0,.3);
  color: #fff !important;
  font-weight: 400 !important;
}

.image-cover {
  height: 100% !important;
  width: 100% !important;
}

.Header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 50;
  padding-bottom: 10px;
}

.AccordionList {
  position: fixed;
  width: 37.5%!important;
  max-width: 400px;
}

.Main-content {
  top: 7rem !important;
  position: relative;
}

blockquote {
  color: #666;
  margin: 1rem 0;
  padding: 0.5rem 0;
  padding-left: 3em;
  border-left: 0.5em #eee solid;
}

img {
  max-width: 100%;
}
